body{
    background: white;
    text-align: center;
    font-size: 12px;
    height: 100vh;
    margin: 25px;
}

svg{
    background: white;
    box-shadow: 2.5px 2.5px 5px #888888;
}

div#tooltip {
    position: absolute;
    text-align: center;
    width: 60px;
    height: auto;
    padding: 5px;
    font: 12px sans-serif;
    background: rgba(255, 255, 255, 0.75);
    border: 0px;
    border-radius: 8px;
    pointer-events: none;
  }
